export default {
	data() {
		return {
			imgUrl: this.imgUrl,
			isLoading: true,
			currentIndex: 0,
			//收藏列表
			storeList: [],
			isEdit: false,
			allSelect: false,
			isNull: false,
			selectCollectList: [],
			page: 1,
			goodsCount: 0,
			articleCount: 0,
			stopLoad: false
		}
	},
	onLoad() {
		this.getGoodsCollect()
	},
	methods: {
		toGoodsDetail(goods_id,shop_id){
			uni.navigateTo({
				url: "../goodsDetail/goodsDetail?id=" + goods_id + "&shop_id=" + shop_id
			})
		},
		getGoodsCollect() {
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/UserCenter/getGoodsCollect",
				data: {
					token: uni.getStorageSync('token'),
					page: this.page,
					pagesize: 8
				},
				method: "POST",
				header: this.header,
				success: (res) => {
					console.log(res.data)
					this.isLoading = false
					if (res.data.status == 0) {
						this.goodsCount = res.data.data.goodsCount
						this.articleCount = res.data.data.articleCount
						this.storeList = res.data.data.list
						return
					}
					this.showToast(res.data.msg)
				}
			})
		},
		loadmore() {
			if (this.stopLoad) {
				return
			}
			this.page++
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/UserCenter/getGoodsCollect",
				data: {
					token: uni.getStorageSync('token'),
					page: this.page,
					pagesize: 8
				},
				method: "POST",
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						if (res.data.data.length == 0) {
							this.stopLoad = true
							return
						}
						for (let i = 0; i < res.data.data.length; i++) {
							this.storeList.push(res.data.data[i])
						}
						return
					}
					this.showToast(res.data.msg)
				}
			})
		},
		//点击导航选项
		chooseOptions(index) {
			this.currentIndex = index
			this.isLoading = true
			this.allSelect = this.isEdit = this.stopLoad = false	
			this.page = 1
			switch (index) {
				case 0:
					this.getGoodsCollect()
					break;
				case 1:
					this.isLoading = false
					this.storeList = []
					break;
				default:
					break;
			}
		},
		//点击编辑
		edit() {
			this.isEdit = !this.isEdit
		},
		//点击勾选当前项
		selectGoods(index) {
			this.storeList[index].checked = !this.storeList[index].checked
			//强制刷新页面
			this.$forceUpdate()
			this.getSelectGoods()
		},
		//全选
		selectAll() {
			this.allSelect = !this.allSelect
			for (let i = 0; i < this.storeList.length; i++) {
				this.storeList[i].checked = this.allSelect
			}
			this.getSelectGoods()
		},
		//获取已勾选的商品
		getSelectGoods() {
			let selectCollect = []
			for (let i = 0; i < this.storeList.length; i++) {
				if (this.storeList[i].checked) {
					selectCollect.push(this.storeList[i])
				}
			}
			if (selectCollect.length == this.storeList.length) {
				this.allSelect = true
			} else {
				this.allSelect = false
			}
			this.selectCollectList = selectCollect
		},
		//批量取消收藏
		cancelCollect() {
			this.isLoading = true
			let selectCollectId = this.selectCollectList.map(v => v.collect_id)
			this.setheader()
			console.log(selectCollectId)
			if (selectCollectId.length == 0) {
				this.showToast('请选择删除项')
				return
			}
			uni.request({
				url: this.websiteUrl + "/api/UserCenter/cancelCollecttions",
				data: {
					collect_ids: selectCollectId.join(','),
					token: uni.getStorageSync('token')
				},
				method: "POST",
				header: this.header,
				success: (res) => {
					if(res.data.status==1){
						this.showToast(res.data.msg)
						switch (this.currentIndex){
							case 0:
							this.page = 1
							this.stopLoad = false
							this.getGoodsCollect()
								break;
							default:
								break;
						}
					}
				}
			})
		}
	},
	onReachBottom() {
		console.log('触底了')
		this.loadmore()
	}
}
